import {STATE} from '../store/actionsType';

export default {
    returnMonthStringFormNumber (number) {
        const str = ['', '', '', '', '', '', '', '', '', '', '', '', ''];
        return str[number];
    },
    returnWeekStringFormNumber (number) {
        const str = ['', '', '', '', '', '', ''];
        return str[number];
    },
    formatString (STATUS) {
        if (STATUS == STATE.FETCHING) return ',...';
        if (STATUS == STATE.SUCCESS) return '';
        if (STATUS == STATE.ERROR) return '';
        return STATUS;
    }
}


