/**
 * 
 * old: dispatch({type:SELECT_MENU,payLoad:{index}})
 * now: dispatch(selectMenu(index));
 */
import actionsType, {SELECT_MENU} from '../actionsType';
export function selectMenu (index) {
    return {type: SELECT_MENU, payLoad: {index}};
}



